"use client";

import React, { useState, useEffect, useRef } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { ScrollArea } from '@/components/ui/scroll-area';
import { useAppContext, useAppActions } from '@/context/AppContext';
import { TopNavigation } from '@/navigation/AppNavigator';
import { cn } from '@/lib/utils';

interface ChatMessage {
  id: string;
  content: string;
  sender: 'user' | 'ai';
  timestamp: Date;
  emotion?: 'supportive' | 'encouraging' | 'calming' | 'motivational';
  suggestions?: string[];
}

interface ConversationScenario {
  id: string;
  title: string;
  description: string;
  category: 'conflict' | 'assertiveness' | 'empathy' | 'boundaries' | 'support';
  difficulty: 'beginner' | 'intermediate' | 'advanced';
  initialMessage: string;
  context: string;
  learningGoals: string[];
}

const conversationScenarios: ConversationScenario[] = [
  {
    id: 'workplace-feedback',
    title: 'Recebendo Feedback no Trabalho',
    description: 'Pratique como receber críticas construtivas de forma equilibrada',
    category: 'assertiveness',
    difficulty: 'intermediate',
    initialMessage: 'Oi! Preciso conversar com você sobre o projeto que você entregou ontem. Há algumas coisas que precisamos melhorar...',
    context: 'Você está no trabalho e seu supervisor quer dar feedback sobre seu desempenho. Pratique receber críticas de forma construtiva.',
    learningGoals: [
      'Ouvir feedback sem se defender imediatamente',
      'Fazer perguntas esclarecedoras',
      'Demonstrar disposição para melhorar'
    ]
  },
  {
    id: 'friend-boundary',
    title: 'Estabelecendo Limites com Amigos',
    description: 'Aprenda a dizer não de forma gentil mas firme',
    category: 'boundaries',
    difficulty: 'intermediate',
    initialMessage: 'Ei, você pode me emprestar dinheiro de novo? Sei que já te pedi várias vezes, mas é só até o final do mês...',
    context: 'Um amigo está pedindo dinheiro emprestado repetidamente. Pratique estabelecer limites saudáveis.',
    learningGoals: [
      'Dizer não de forma respeitosa',
      'Explicar seus limites claramente',
      'Manter o relacionamento enquanto protege seus interesses'
    ]
  },
  {
    id: 'family-conflict',
    title: 'Conflito Familiar',
    description: 'Navegue conversas difíceis com familiares de forma empática',
    category: 'conflict',
    difficulty: 'advanced',
    initialMessage: 'Você nunca me visita mais! Parece que não se importa com a família desde que se mudou...',
    context: 'Um familiar está expressando sentimentos de abandono. Pratique responder com empatia enquanto mantém seus limites.',
    learningGoals: [
      'Validar sentimentos sem assumir culpa desnecessária',
      'Expressar seu ponto de vista com calma',
      'Encontrar soluções que funcionem para ambos'
    ]
  },
  {
    id: 'support-friend',
    title: 'Apoiando um Amigo em Crise',
    description: 'Pratique oferecer suporte emocional de forma efetiva',
    category: 'support',
    difficulty: 'beginner',
    initialMessage: 'Estou passando por um momento muito difícil... Não sei mais o que fazer. Sinto que tudo está dando errado na minha vida.',
    context: 'Um amigo está passando por dificuldades e precisa de apoio. Pratique ser um bom ouvinte e oferecer suporte adequado.',
    learningGoals: [
      'Praticar escuta ativa',
      'Oferecer validação emocional',
      'Evitar dar conselhos não solicitados'
    ]
  },
  {
    id: 'romantic-disagreement',
    title: 'Desentendimento Romântico',
    description: 'Resolva conflitos em relacionamentos íntimos com maturidade',
    category: 'conflict',
    difficulty: 'advanced',
    initialMessage: 'Estou chateado(a) porque você cancelou nossos planos de novo. Sinto que não sou prioridade na sua vida...',
    context: 'Seu parceiro(a) está expressando frustração sobre prioridades no relacionamento. Pratique comunicação empática.',
    learningGoals: [
      'Reconhecer e validar sentimentos do parceiro',
      'Expressar suas necessidades claramente',
      'Trabalhar juntos para encontrar soluções'
    ]
  }
];

export default function SocialSimulatorScreen() {
  const { state } = useAppContext();
  const { addPoints, setError } = useAppActions();
  
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [inputMessage, setInputMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [selectedScenario, setSelectedScenario] = useState<ConversationScenario | null>(null);
  const [conversationStarted, setConversationStarted] = useState(false);
  const [showFeedback, setShowFeedback] = useState(false);
  
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);

  // Auto scroll para a última mensagem
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  // Foca no input quando a conversa inicia
  useEffect(() => {
    if (conversationStarted && inputRef.current) {
      inputRef.current.focus();
    }
  }, [conversationStarted]);

  const startScenario = (scenario: ConversationScenario) => {
    setSelectedScenario(scenario);
    setMessages([
      {
        id: '1',
        content: scenario.initialMessage,
        sender: 'ai',
        timestamp: new Date(),
        emotion: 'supportive'
      }
    ]);
    setConversationStarted(true);
    setShowFeedback(false);
  };

  const sendMessage = async () => {
    if (!inputMessage.trim() || isLoading) return;

    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      content: inputMessage,
      sender: 'user',
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage('');
    setIsLoading(true);
    setError(null);

    try {
      const response = await fetch('/api/simulation/ai', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message: inputMessage,
          context: selectedScenario?.context,
          scenario: selectedScenario?.id
        }),
      });

      if (!response.ok) {
        throw new Error('Erro na comunicação com o simulador');
      }

      const data = await response.json();

      if (data.success) {
        const aiMessage: ChatMessage = {
          id: (Date.now() + 1).toString(),
          content: data.reply,
          sender: 'ai',
          timestamp: new Date(),
          emotion: data.emotion,
          suggestions: data.suggestions
        };

        setMessages(prev => [...prev, aiMessage]);
        
        // Adiciona pontos por participação
        addPoints(5);
      } else {
        throw new Error(data.reply || 'Erro desconhecido');
      }

    } catch (error) {
      console.error('Erro ao enviar mensagem:', error);
      setError('Não foi possível enviar a mensagem. Tente novamente.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const endConversation = () => {
    setShowFeedback(true);
    addPoints(20); // Bonus por completar cenário
  };

  const resetSimulator = () => {
    setSelectedScenario(null);
    setMessages([]);
    setConversationStarted(false);
    setShowFeedback(false);
    setInputMessage('');
  };

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'conflict': return 'bg-red-100 text-red-800';
      case 'assertiveness': return 'bg-blue-100 text-blue-800';
      case 'empathy': return 'bg-green-100 text-green-800';
      case 'boundaries': return 'bg-purple-100 text-purple-800';
      case 'support': return 'bg-yellow-100 text-yellow-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'beginner': return 'bg-green-100 text-green-800';
      case 'intermediate': return 'bg-yellow-100 text-yellow-800';
      case 'advanced': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getEmotionColor = (emotion?: string) => {
    switch (emotion) {
      case 'supportive': return 'border-l-green-400';
      case 'encouraging': return 'border-l-blue-400';
      case 'calming': return 'border-l-purple-400';
      case 'motivational': return 'border-l-orange-400';
      default: return 'border-l-gray-400';
    }
  };

  // Tela de feedback pós-conversa
  if (showFeedback && selectedScenario) {
    return (
      <div className="min-h-screen bg-gradient-soft">
        <TopNavigation title="Feedback da Conversa" />
        
        <div className="container-mobile py-6 space-y-6">
          <div className="text-center space-y-4">
            <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mx-auto">
              <div className="w-8 h-8 bg-green-500 rounded-full" />
            </div>
            <h1 className="text-2xl font-bold text-gray-900">
              Conversa Concluída!
            </h1>
            <p className="text-gray-600">
              Você praticou: {selectedScenario.title}
            </p>
          </div>

          <Card className="card-elevated">
            <CardHeader>
              <CardTitle>Objetivos de Aprendizado</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              {selectedScenario.learningGoals.map((goal, index) => (
                <div key={index} className="flex items-start space-x-3">
                  <div className="w-6 h-6 bg-green-100 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                    <div className="w-3 h-3 bg-green-500 rounded-full" />
                  </div>
                  <span className="text-sm text-gray-700">{goal}</span>
                </div>
              ))}
            </CardContent>
          </Card>

          <Card className="card-soft">
            <CardContent className="pt-4 text-center space-y-3">
              <div className="text-2xl font-bold text-green-600">
                +25 pontos
              </div>
              <p className="text-sm text-gray-600">
                {messages.length} mensagens trocadas
              </p>
              <p className="text-xs text-gray-500">
                Continue praticando para desenvolver suas habilidades sociais!
              </p>
            </CardContent>
          </Card>

          <div className="space-y-3">
            <Button 
              className="w-full btn-primary"
              onClick={resetSimulator}
            >
              Praticar Outro Cenário
            </Button>
            <Button 
              variant="outline" 
              className="w-full"
              onClick={() => window.location.href = '/'}
            >
              Voltar ao Início
            </Button>
          </div>
        </div>
      </div>
    );
  }

  // Tela da conversa ativa
  if (conversationStarted && selectedScenario) {
    return (
      <div className="min-h-screen bg-gradient-soft flex flex-col">
        <TopNavigation 
          title={selectedScenario.title}
          showBack={true}
          onBack={resetSimulator}
        />
        
        {/* Context Info */}
        <div className="container-mobile py-2">
          <Card className="card-soft border-l-4 border-l-blue-500">
            <CardContent className="pt-3 pb-3">
              <p className="text-sm text-blue-800">
                <strong>Contexto:</strong> {selectedScenario.context}
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Messages Area */}
        <div className="flex-1 container-mobile pb-4">
          <ScrollArea className="h-full">
            <div className="space-y-4 pb-4">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={cn(
                    "flex",
                    message.sender === 'user' ? 'justify-end' : 'justify-start'
                  )}
                >
                  <div
                    className={cn(
                      "max-w-[80%] rounded-lg p-3 space-y-2",
                      message.sender === 'user'
                        ? 'bg-blue-500 text-white'
                        : cn('bg-white border-l-4', getEmotionColor(message.emotion))
                    )}
                  >
                    <p className="text-sm">{message.content}</p>
                    
                    {message.suggestions && message.suggestions.length > 0 && (
                      <div className="space-y-1">
                        <p className="text-xs opacity-75 font-medium">Sugestões:</p>
                        {message.suggestions.map((suggestion, index) => (
                          <div key={index} className="text-xs opacity-90 bg-black/5 p-2 rounded">
                            • {suggestion}
                          </div>
                        ))}
                      </div>
                    )}
                    
                    <div className="text-xs opacity-75">
                      {message.timestamp.toLocaleTimeString('pt-BR', { 
                        hour: '2-digit', 
                        minute: '2-digit' 
                      })}
                    </div>
                  </div>
                </div>
              ))}
              
              {isLoading && (
                <div className="flex justify-start">
                  <div className="bg-white border-l-4 border-l-gray-400 rounded-lg p-3">
                    <div className="flex space-x-1">
                      <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" />
                      <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }} />
                      <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }} />
                    </div>
                  </div>
                </div>
              )}
              
              <div ref={messagesEndRef} />
            </div>
          </ScrollArea>
        </div>

        {/* Error Alert */}
        {state.error && (
          <div className="container-mobile pb-2">
            <Alert className="border-red-200 bg-red-50">
              <AlertDescription className="text-red-800">
                {state.error}
              </AlertDescription>
            </Alert>
          </div>
        )}

        {/* Input Area */}
        <div className="container-mobile pb-6 safe-area-bottom">
          <div className="flex space-x-2">
            <Input
              ref={inputRef}
              value={inputMessage}
              onChange={(e) => setInputMessage(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder="Digite sua resposta..."
              disabled={isLoading}
              className="flex-1"
            />
            <Button
              onClick={sendMessage}
              disabled={!inputMessage.trim() || isLoading}
              className="btn-primary"
            >
              {isLoading ? '...' : 'Enviar'}
            </Button>
          </div>
          
          <div className="flex justify-center mt-3">
            <Button
              variant="outline"
              size="sm"
              onClick={endConversation}
              className="text-xs"
            >
              Finalizar Conversa
            </Button>
          </div>
        </div>
      </div>
    );
  }

  // Tela principal - seleção de cenários
  return (
    <div className="min-h-screen bg-gradient-soft">
      <TopNavigation 
        title="Simulador Social" 
        showBack={true}
        onBack={() => window.history.back()}
      />
      
      <div className="container-mobile py-6 pb-24 space-y-6">
        <div className="text-center space-y-2">
          <h1 className="text-2xl font-bold text-gray-900">
            Pratique Suas Habilidades Sociais
          </h1>
          <p className="text-gray-600">
            Escolha um cenário para praticar conversas em um ambiente seguro
          </p>
        </div>

        <div className="space-y-4">
          {conversationScenarios.map((scenario) => (
            <Card 
              key={scenario.id} 
              className="card-soft hover:shadow-md transition-all duration-200 cursor-pointer"
              onClick={() => startScenario(scenario)}
            >
              <CardContent className="pt-4">
                <div className="space-y-3">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <div className="flex items-center space-x-2 mb-2">
                        <Badge className={cn("text-xs", getCategoryColor(scenario.category))}>
                          {scenario.category}
                        </Badge>
                        <Badge className={cn("text-xs", getDifficultyColor(scenario.difficulty))}>
                          {scenario.difficulty}
                        </Badge>
                      </div>
                      
                      <h3 className="font-semibold text-gray-900 mb-1">
                        {scenario.title}
                      </h3>
                      <p className="text-sm text-gray-600">
                        {scenario.description}
                      </p>
                    </div>
                    
                    <div className="ml-4 text-blue-500">
                      →
                    </div>
                  </div>

                  <div className="space-y-2">
                    <h4 className="text-xs font-medium text-gray-700">
                      Você vai praticar:
                    </h4>
                    <ul className="space-y-1">
                      {scenario.learningGoals.slice(0, 2).map((goal, index) => (
                        <li key={index} className="text-xs text-gray-600 flex items-start">
                          <span className="text-blue-500 mr-2">•</span>
                          {goal}
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* Free Chat Option */}
        <Card className="card-soft border-2 border-dashed border-blue-200">
          <CardContent className="pt-4 text-center space-y-3">
            <h3 className="font-semibold text-gray-900">
              Conversa Livre
            </h3>
            <p className="text-sm text-gray-600">
              Converse livremente com o assistente sobre qualquer tópico emocional
            </p>
            <Button 
              variant="outline" 
              className="w-full"
              onClick={() => {
                const freeChat: ConversationScenario = {
                  id: 'free-chat',
                  title: 'Conversa Livre',
                  description: 'Conversa aberta sobre bem-estar emocional',
                  category: 'support',
                  difficulty: 'beginner',
                  initialMessage: 'Olá! Como você está se sentindo hoje? Estou aqui para conversar sobre qualquer coisa que esteja em sua mente.',
                  context: 'Uma conversa livre e aberta sobre bem-estar emocional',
                  learningGoals: ['Expressar sentimentos livremente', 'Receber apoio emocional']
                };
                startScenario(freeChat);
              }}
            >
              Iniciar Conversa Livre
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
